<template>
    <div class="TestWord students">
        <div>
          <crumb style="padding: 0 5px;"></crumb>
        </div>
        <div v-for="(item, indexq) in AllList" :key="indexq">
            <div class="ooooo">
                <img src="@/assets/img/kecheng.png" alt="" >
                <p class="TestWord-btn">{{item.examine}}</p>
            </div>   
            <div class="lplplp">
                <table cellspacing="0"  cellpadding="0"  border="0" class="el-table el-table--border">
                        <colgroup>
                            <col/>
                            <col/>
                            <col  v-for="(res, index) in item.tableHeader" :key="index">
                        </colgroup>
                        <tr>
                            <th style="padding-left: 20px; width:60px;">编号</th>
                            <th style="padding-left: 20px; width:200px;" v-for="(items, index) in item.tableHeader" :key="index">{{items.goalName}}</th>
                        </tr>
                        <tr v-for="(iteme, indexs) in item.table" :key="indexs">
                            <td style="padding-left: 20px; " @mousemove="addad" @mouseleave="assaf">{{indexs +1 }}
                                <span class="table-sop" v-show="falge3"><i class="el-icon-circle-close" @click="deleteexam(iteme.examId,indexs,indexq)"></i></span>
                            </td>
                            <td style="width: 189px;" >
                                <input type="text" v-model="iteme.examName" autofocus="autofocus" ref='id' style="height: 30px;margin-left: 8px; border: 0;">
                            </td>
                            <td v-for="(item, index) in iteme.relations" :key="index" @click="change(indexs,index,item,indexq)">
                                <i v-if="item.success" class="el-icon-check" style="text-align: center;display: block;font-size: 26px;"></i>
                            </td>
                        </tr>
                </table>
            </div>
            <div class="ppppp" @click="addLine(indexq)">
                <img src="@/assets/img/tianjiatimu.png" alt="" >
                <p  class="TestWord-btn"> 添加考察题目</p><br><br><br>
            </div>
        </div>
        <div style="overflow: hidden;margin-bottom: 120px;">
            <el-button @click="preserved" class="TestWord-btn1">保存</el-button>
        </div>
    </div>
</template>
<script>
export default {
    name: "TestWorld",
    data(){
        return{
            AllList:[],
            falge3:false,
        }
    },
    methods:{
        examgoalAll(){
            let id = this.$route.query.id
            this.$get(`/teacher/examgoal/all/${id}`).then(res=>{
                this.AllList = res
                this.AllList.forEach(item=>{
                    item.tableHeader=JSON.parse(JSON.stringify(item.table[0].relations))
                    item.table.splice(0,1)
                })
                console.log(this.AllList,'this.AllList')
            })
        },
        change(a,b,c,indexq){
            this.AllList[indexq].table[a].relations.forEach(item=>{
                if(c.examId == item.examId && c.goalId == item.goalId){
                    item.success = !item.success
                }else{
                    item.success = false
                }
            })
        },
        addLine(indexq) {
            // this.$refs.id.focus();
        //添加行数 期末成绩  /teacher/examgoal/addexam/{courseId}/{examineId}
            let topRelations = JSON.parse(JSON.stringify( this.AllList[indexq].tableHeader))
            topRelations.splice(0,1)
            this.$post(`/teacher/examgoal/addexam/${this.$route.query.id}/${this.AllList[indexq].examineId}`).then(res=>{
                var newValue = {
                        examGoalId:"",
                        examId: "",
                        relations:topRelations 
                    };
                newValue.examId =  res.examID
                newValue.relations.forEach((arr,index)=>{
                    arr.examId = res.examID
                    arr.goalName = ''
                })
                this.AllList[indexq].table.push(newValue);
            })
        },
        //  删除   /teacher/examgoal/deleteexam   examId
        deleteexam(examId,index,inde){
            this.$deletes(`teacher/examgoal/deleteexam/${examId}`).then(res=>{
                if(res.success== true){
                    this.AllList[inde].table.splice(index, 1);
                }
            })
        },
        addad(){
            this.falge3 = true
        },
        assaf(){
            this.falge3 = false
        },
        preserved(){   
            let keywords = []
            this.AllList.forEach((item,index)=>{
                item.table.forEach((items,index)=>{
                    items.examineId = item.examineId
                    keywords.push(items)
                })
            })
            this.$post('/teacher/examgoal/submitall',keywords).then(res=>{
                if(res.success == true){
                    this.$notify.success({
                        title: 'success',
                        message: "保存成功"
                    });
                    this.$router.go(-1)
                }
            })
        }
    },
    mounted(){
        this.examgoalAll()
    }
}
</script>
<style lang="scss" >
.TestWord {
    // padding: 0 40px;
    .el-table td, .el-table th{
        padding: 6px 0;
        border:0.5px solid #EBEEF5;
    }
    .ppppp{
        width: 150px;
        cursor: pointer;
        img{
            float: left;
            width: 25px;
            display: block;
            // margin-top: 15px;
            margin-right: 10px;
        }
    }
    .TestWord-btn{
      text-align: left;
      float: left;
      margin-bottom: 20px;
    }
    .lplplp{
      position: relative;
      padding-bottom: 15px;
    }
    .table-sop{
        position: absolute;
        left: 60px;
		cursor: pointer;
      }
    .ooooo{
       text-align: left;
        // float: left;
        img{
            float: left;
            width: 25px;
            display: block;
            // margin-top: 15px;
            margin-right: 10px;
        }
    }
    input:focus { outline: none; } 
    .el-table th, .el-table tr{
      height: 48px;
    }
    .TestWord-btn1{
      float: right;
      margin-right: 300px;
    //   margin-bottom: 20px;
    }
    .TestWord-img{
      float: left;
      margin-top: 20px;
      text-align: left;
      display: block;
      margin-right: 12px;
      margin-bottom: 20px;
    //   width: 1.5%;
    }
  .el-input__inner {
    border: 0;
  }
}
</style>


